/**
 * YouTube Music API Proxy - TypeScript Library
 * Complete API library for interacting with YouTube Music API Proxy
 * 
 * This file provides the core API functionality with proper classes and interfaces
 * that mirror the C# backend models for type safety and consistency.
 */

// ============================================================================
// Base Classes and Interfaces
// ============================================================================

/**
 * Base entity with name and ID
 */
export abstract class NamedEntity {
    constructor(
        public readonly name: string,
        public readonly id: string
    ) {}
}

/**
 * Base entity with additional info
 */
export abstract class EntityInfo extends NamedEntity {
    constructor(
        name: string,
        id: string,
        public readonly thumbnails: Thumbnail[]
    ) {
        super(name, id);
    }
}

/**
 * Thumbnail information
 */
export interface Thumbnail {
    url: string;
    width: number;
    height: number;
}

/**
 * Radio channel information
 */
export interface Radio {
    playlistId: string;
    name: string;
}

/**
 * Playability status
 */
export interface PlayabilityStatus {
    status: string;
    reason?: string;
}

/**
 * Playback tracking information
 */
export interface PlaybackTracking {
    videostatsPlaybackUrl: string;
    videostatsDelayplayUrl: string;
    videostatsWatchtimeUrl: string;
    ptrackingUrl: string;
    qoeUrl: string;
    atrUrl: string;
}

/**
 * Stream information base
 */
export interface StreamInfo {
    type: 'AudioStreamInfo' | 'VideoStreamInfo';
    url: string;
    bitrate: number;
    contentLenght: number;
}

/**
 * Audio stream information
 */
export interface AudioStreamInfo extends StreamInfo {
    type: 'AudioStreamInfo';
}

/**
 * Video stream information
 */
export interface VideoStreamInfo extends StreamInfo {
    type: 'VideoStreamInfo';
    width: number;
    height: number;
    fps: number;
}

/**
 * Streaming data containing audio and video streams
 */
export interface StreamingData {
    streamInfo: StreamInfo[];
}

/**
 * Search category enumeration
 */
export enum SearchCategory {
    Songs = 'Songs',
    Videos = 'Videos',
    Albums = 'Albums',
    CommunityPlaylists = 'CommunityPlaylists',
    Artists = 'Artists',
    Podcasts = 'Podcasts',
    Episodes = 'Episodes',
    Profiles = 'Profiles'
}

// ============================================================================
// Search Result Classes
// ============================================================================

/**
 * Base search result class
 */
export abstract class SearchResult extends EntityInfo {
    constructor(
        name: string,
        id: string,
        thumbnails: Thumbnail[],
        public readonly category: SearchCategory
    ) {
        super(name, id, thumbnails);
    }
}

/**
 * Song search result
 */
export class SongSearchResult extends SearchResult {
    constructor(
        name: string,
        id: string,
        public readonly artists: NamedEntity[],
        public readonly album: NamedEntity,
        public readonly duration: string,
        public readonly isExplicit: boolean,
        public readonly playsInfo: string,
        public readonly radio: Radio | null,
        thumbnails: Thumbnail[]
    ) {
        super(name, id, thumbnails, SearchCategory.Songs);
    }
}

/**
 * Video search result
 */
export class VideoSearchResult extends SearchResult {
    constructor(
        name: string,
        id: string,
        public readonly artists: NamedEntity[],
        public readonly duration: string,
        public readonly isExplicit: boolean,
        public readonly playsInfo: string,
        public readonly radio: Radio | null,
        thumbnails: Thumbnail[]
    ) {
        super(name, id, thumbnails, SearchCategory.Videos);
    }
}

/**
 * Album search result
 */
export class AlbumSearchResult extends SearchResult {
    constructor(
        name: string,
        id: string,
        public readonly artists: NamedEntity[],
        public readonly year: string,
        public readonly songCount: number,
        thumbnails: Thumbnail[]
    ) {
        super(name, id, thumbnails, SearchCategory.Albums);
    }
}

/**
 * Artist search result
 */
export class ArtistSearchResult extends SearchResult {
    constructor(
        name: string,
        id: string,
        public readonly subscribers: string,
        public readonly songCount: number,
        thumbnails: Thumbnail[]
    ) {
        super(name, id, thumbnails, SearchCategory.Artists);
    }
}

